@use "global" as *;

.about__wrapper {
    @include mq(md) {
        background-image: url("../images/aboutrose.jpg");
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        // max-width: rem(1440);
        width: 100%;
        padding-top: rem(72);
        padding-bottom: rem(66);
        margin-right: auto;
        margin-left: auto;
    }
}

.about__title-area {
    background-image: url("../images/aboutrose.jpg");
    // max-width: rem(375);
    width: 100%;
    height: rem(100);
    // display: inline-block;
    margin-right: auto;
    margin-left: auto;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    padding-top: rem(19);
    @include mq(md) {
        background-image: none;
    }
}

.about__text-wrapper {
    margin-top: rem(24);
    max-width: rem(320);
    width: 100%;
    margin-right: auto;
    margin-left: auto;
    @include mq(md) {
        margin-top: rem(23);
        max-width: rem(610);
    }
}

.about__text {
    font-size: rem(14);
    line-height: calc(24 / 14);
    font-weight: $medium;
    @include mq(md) {
        font-size: rem(24);
        line-height: calc(41 / 24);
        text-align: center;
    }
}

.about__lists {
    margin-top: rem(6);
    width: 100%;
    padding-right: $padding-sp;
    padding-left: $padding-sp;
    margin-right: auto;
    margin-left: auto;
    @include mq(md) {
        margin-top: rem(80);
        max-width: $inner + strip-unit($padding-pc) * 2;
        padding-right: $padding-pc;
        padding-left: $padding-pc;
    }
}





