@use "global" as *;

// section title
.title__wrapper {
    overflow: hidden;
    --scaleX: 0;
    transform: scaleX(var(--scaleX)); /*アニメーションの下準備*/
}
.title {
    transform: translateY(115%); /*アニメーションの下準備*/
}

// cafe menu
.js-title-effect{
    position: relative;
    transform: translateY(20px); // オシャレポイント(少し下からふわっと)
    opacity: 0;
    visibility: hidden;
    overflow: hidden;
    --translateX:0px;
}

.js-title-effect::before{
    content: '';
    width: 100%;
    height: 100%;
    background-color: #df8686;
    position: absolute;
    top: 0;
    left: 0;
    transform: translateX(var(--translateX));
}

.js-title-effect-trigger{
    width: 100%;
    @include mq(md) {
        max-width: $inner + strip-unit($padding-pc) * 2;
        padding-right: $padding-pc;
        padding-left: $padding-pc;
    }
}

// opening
.opening {
    width: 100%;
    height: 100vh;
    position: relative;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99;

    display: flex;
    align-items: center;
    justify-content: center;
    
}

.opening__mask {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(255,255,255,1);
    z-index: 1;
    
}

.opening__logo {
    display: block;
    width: rem(300);
    z-index: 2;
    @include mq(md){
        width: rem(500);
    }
}