@use "global" as *;

.insta {
    background-color: #f4f4f4;
    padding: rem(41) 0 rem(40);
    @include mq(md) {
        padding: rem(72) 0 rem(80);
    }
}

.insta__inner {
    margin-top: rem(26);
    width: 100%;
    padding-right: $padding-sp;
    padding-left: $padding-sp;
    margin-right: auto;
    margin-left: auto;
    max-width: rem(500);
    @include mq(md) {
        margin-top: rem(83);
        max-width: initial;
    }
}

.insta__img-bg {
    @include mq(md) {
        background-color: $white;
        max-width: rem(1000);
        width: 100%;
        height: rem(996);
        margin-right: auto;
        margin-left: auto;
        padding-top: rem(48);
    }
}

.insta__img-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    @include mq(md) {
        max-width: rem(900);
        width: 100%;
        margin-right: auto;
        margin-left: auto;
    }
}

.insta__img img {
    width: rem(125);
    @include mq(md) {
        width: rem(300);
    }
}
