@use "global" as *;

.about-lists {
    display: flex;
    flex-wrap: wrap;
}

.about-lists__item {
    width: calc(100% / 2 - #{rem(55)}/ 2);
    @include mq(md) {
        width: calc(100% / 3 - #{rem(380)}/ 3);
    }
}

.about-lists__item:nth-child(2n) {
    margin-left: rem(55);
    @include mq(md) {
        margin-left: 0;
    }
}

.about-lists__item:nth-child(n + 3) {
    margin-top: rem(32);
    @include mq(md) {
        margin-top: 0;
    }
}

.about-lists__item:nth-child(n + 2) {
    @include mq(md) {
        margin-left: rem(190);
    }
}
