.about-list__img img {
    border-radius: 50%;
    aspect-ratio: 1 / 1;
    width: 100%;
    object-fit: cover;
}

.about-list__title {
    font-size: rem(24);
    line-height: 1;
    font-weight: $semi-bold;
    color: $brown;
    text-align: center;
    margin-top: rem(26);
    @include mq(md) {
        font-size: rem(36);
        margin-top: rem(22);
    }
}

.about-list__text {
    font-size: rem(14);
    line-height: calc(24 / 14);
    font-weight: $semi-bold;
    margin-top: rem(28);
    @include mq(md) {
        font-size: rem(16);
        line-height: calc(27 / 16);
        margin-top: rem(34);
    }
}
