.look__inner .swiper-pagination{
    position: relative;
}

// .swiper-horizontal > .swiper-pagination-bullets,
// .swiper-pagination-bullets.swiper-pagination-horizontal,
// .swiper-pagination-custom,
// .swiper-pagination-fraction {
//     bottom: -25px;
// }

.look__inner .swiper-pagination-bullets.swiper-pagination-horizontal {
    bottom: rem(-8.2);
    @include mq(md){
        bottom: rem(-16);
    }
}

.look__inner
    .swiper-pagination-bullets.swiper-pagination-horizontal
    .swiper-pagination-bullet {
    background-color: transparent;
    border: 1px solid $gray;
    opacity: 1;
    width: rem(8);
    height: rem(8);
    @include mq() {
        width: rem(15);
        height: rem(15);
    }
}

.look__inner
    .swiper-pagination-bullets.swiper-pagination-horizontal
    .swiper-pagination-bullet.swiper-pagination-bullet-active {
    background-color: $gray;
}

//.swiper-slide-activeを持っていないものは、0.8倍にする
.look__inner .swiper-slide:not(.swiper-slide-active) {
    @include mq() {
        transform: scale(0.8);
        transition: 1s;
    }
}

.look__inner .swiper-slide.swiper-slide-active{
    @include mq(md){
        transition: 1s;
    }
}