@use "global" as *;

.footer__page-top {
    display: none;
    @include mq(md) {
        margin-top: rem(63);
        display: flex;
        justify-content: flex-end;
        max-width: $inner + strip-unit($padding-pc) * 2;
        margin-right: auto;
        margin-left: auto;
        padding-right: $padding-pc;
        padding-left: $padding-pc;
        z-index: 10;
        position: fixed;
        bottom: 30px;
        right: 20px;
    }
}

.footer__page-top img {
    @include mq(md) {
        max-width: rem(60);
        width: 100%;
    }
}

.footer__inner {
    @include mq(md) {
        margin-top: rem(160);
    }
}

.footer__bg {
    @include mq(md) {
        background-image: url(../images/aboutrose-bg2.png);
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        width: 100%;
        height: rem(470);
    }
}

.footer__logo {
    background-image: url(../images/aboutrose-bg2.png);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    width: 100%;
    height: rem(122.4);
    @include mq(md) {
        background-image: none;
        max-width: $inner + strip-unit($padding-pc) * 2;
        padding-right: $padding-pc;
        padding-left: $padding-pc;
        margin-right: auto;
        margin-left: auto;
        width: 100%;
        height: initial;
    }
}

.footer__logo img {
    max-width: rem(155.17);
    width: 100%;
    padding-left: $padding-sp;
    padding-top: rem(35.4);
    @include mq(md) {
        max-width: rem(259.3);
        padding-left: 0;
        padding-top: rem(80);
    }
}

.footer__info {
    margin-top: rem(26);
    color: $brown;
    font-size: rem(14);
    line-height: 1;
    font-weight: $medium;
    @include mq(md) {
        margin-top: rem(18);
        font-size: rem(16);
    }
}

.footer__info-address {
    @include mq(md) {
        display: flex;
    }
}

.footer__info-address dt {
    @include mq(md) {
        padding-right: rem(30);
    }
}

.footer__info-address dd {
    @include mq(md) {
        display: flex;
    }
}

.footer__info-address dd p:nth-child(n + 2) {
    padding-top: rem(6);
    @include mq(md) {
        padding-top: 0;
        padding-left: rem(17);
    }
}

.footer__info-area {
    margin-top: rem(12);
    display: flex;
    @include mq(md) {
        margin-top: rem(13);
    }
}

.footer__tel-title {
    padding-right: rem(30);
    @include mq(md) {
        padding-right: rem(45);
    }
}

.footer__mail-title {
    padding-right: rem(20);
    @include mq(md) {
        padding-right: rem(35);
    }
}

.footer__bottom-wrapper {
    margin-top: rem(81);
    color: $brown;
    display: flex;
    justify-content: space-between;
    @include mq(md) {
        margin-top: rem(160);
        align-items: center;
        flex-direction: row-reverse;
    }
}

.footer__bottom-nav {
    font-size: rem(14);
    line-height: 1;
    font-weight: $medium;
    display: flex;
    flex-direction: column;
    @include mq(md) {
        flex-direction: row;
        font-size: rem(16);
        padding-right: rem(102);
    }
}

.footer__bottom-nav a:nth-child(n + 2) {
    padding-top: rem(8);
    @include mq(md) {
        padding-top: 0;
        padding-left: rem(18);
    }
}

.footer__bottom-nav a::before {
    content: "▶︎";
    width: rem(13);
    height: rem(11);
    padding-right: rem(8);
}

.footer__bottom-icon {
    max-width: rem(84.28);
    width: 100%;
    display: flex;
    justify-content: space-between;
    @include mq(md) {
        max-width: rem(171.5);
        align-items: center;
    }
}

.footer__copyright {
    margin-top: rem(42);
    text-align: center;
    background-color: $pink;
    padding: rem(12) 0;
    @include mq(md) {
        margin-top: 0;
        padding: rem(11) 0 rem(9);
    }
}

.footer__copyright small {
    font-size: rem(14);
    line-height: 1;
    font-weight: $medium;
    color: $white;
    @include mq(md) {
        font-size: rem(16);
    }
}
