@use "global" as *;

.shop__inner {
    padding-right: $padding-sp;
    padding-left: $padding-sp;
    width: 100%;
    margin-right: auto;
    margin-left: auto;
    @include mq(md) {
        max-width: $inner + strip-unit($padding-pc) * 2;
        padding-right: $padding-pc;
        padding-left: $padding-pc;
    }
}

.shop__bg {
    background-color: $beige;
    padding-top: rem(32);
    padding-bottom: rem(32);
    @include mq(md) {
        padding-top: rem(72);
        padding-bottom: rem(83);
    }
}

.shop__lists {
    margin-top: rem(34);
    max-width: min(calc(300 / 375 * 100vw), 500px);
    width: 100%;
    margin-right: auto;
    margin-left: auto;
    @include mq(md) {
        margin-top: rem(75);
        max-width: initial;
    }
}
