@use "global" as *;

.news-lists {
    display: block;
    @include mq(md) {
        display: flex;
        align-items: flex-start;
        flex-wrap: wrap;
    }
}

.news-lists__item {
    @include mq(md) {
        width: calc(100% / 3 - #{rem(40)} * 2 / 3);
    }
}

.news-lists__item:nth-child(n + 2) {
    margin-top: rem(34);
    @include mq(md) {
        margin-top: 0;
    }
}

.news-lists__item:nth-child(n + 4) {
    @include mq(md) {
        margin-left: 0;
        margin-top: rem(83);
    }
}

.news-lists__item:not(:nth-child(3n + 1)) {
    @include mq(md) {
        margin-left: rem(40);
    }
}
