@use "global" as *;

.news-list {
    @include mq(md) {
        max-width: rem(400);
        width: 100%;
    }
}

.news-list__img img {
    aspect-ratio: 300 / 165;
    width: 100%;
    object-fit: cover;
    border-radius: rem(40) 0 rem(40) 0;
    @include mq(md) {
        aspect-ratio: 400 / 220;
    }
}

.news-list__content {
    margin-top: rem(16);
    @include mq(md) {
        margin-top: rem(24);
    }
}

.news-list__date {
    font-family: $sub-font-family;
    font-size: rem(14);
    font-weight: $medium;
    line-height: 1;
    background-color: $brown;
    color: $white;
    padding: rem(6) rem(11) rem(5) rem(10);
}

.news-list__text {
    font-size: rem(14);
    line-height: calc(24 / 14);
    font-weight: $semi-bold;
    margin-top: rem(16);
    @include mq(md) {
        font-size: rem(16);
        line-height: calc(27 / 16);
        margin-top: rem(8);
    }
}
