@use "global" as *;

.shop-lists {
    @include mq(md) {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        column-gap: rem(40);
        row-gap: rem(40);
    }
}

.shop-lists__item:nth-child(n + 2) {
    margin-top: rem(32);
    @include mq(md) {
        margin-top: 0;
    }
}
