@use "global" as *;

.section-title-wrapper {
    text-align: center;
}

.section-title {
    font-family: $en-font-family;
    font-size: rem(24);
    font-weight: $regular;
    line-height: calc(32 / 24);
    color: $brown;
    display: inline-block;
    border-bottom: rem(5) solid $pink;
    @include mq(md) {
        font-size: rem(36);
        line-height: calc(48 / 36);
    }
}

.section-ja-title {
    font-size: rem(14);
    font-weight: $semi-bold;
    line-height: 1;
    color: $gray;
    margin-top: rem(2);
    display: block;
    @include mq(md) {
        font-size: rem(16);
    }
}
