@use "global" as *;

.tab {
    // max-width: rem(600);
    width: 100%;
    margin: 0 auto;
    margin-top: rem(24);
    @include mq(md) {
        max-width: $inner + strip-unit($padding-pc) * 2;
        padding-right: $padding-pc;
        padding-left: $padding-pc;
        margin-top: rem(36);
    }
}

.tab__btn-area {
    display: flex;
}

.tab__btn {
    width: 33.3333%;
    text-align: center;
    padding: rem(24) 0;
    cursor: pointer;
    font-family: $en-font-family;
    font-size: rem(24);
    line-height: calc(32 / 24);
    // font-weight: $regular;
    color: $brown;
    border: 1px solid $pink;
    @include mq(md) {
        width: 100%;
        padding: rem(14) 0 rem(18);
        font-size: rem(36);
        line-height: calc(48 / 36);
    }
}

.tab__body {
    background-color: $pink;
    color: $white;
    padding: rem(82) rem(28) rem(40) rem(19);
    @include mq(md) {
        padding: rem(78) rem(49) rem(80) rem(51);
    }
}

.tab__contents {
    display: none;
}

.tab__title {
    font-size: rem(28);
    line-height: 1;
    font-weight: $semi-bold;
    margin-bottom: rem(24);
    border-bottom: 4px solid $white;
    display: inline-block;
    padding-bottom: rem(8);
    @include mq(md) {
        font-size: rem(36);
        margin-bottom: rem(62);
        border-bottom: 8px solid $white;
        padding-bottom: rem(7);
    }
}

.tab__list {
    font-size: rem(24);
    line-height: 1;
    font-weight: $medium;
    border-bottom: 1px solid $white;
    padding-bottom: rem(20);
    margin-top: rem(19);
    @include mq(md) {
        font-size: rem(36);
        padding-bottom: rem(23);
        margin-top: rem(22);
    }
}

.js-tab-btn {
    background-color: $pink;
}

.js-contents-active {
    display: block;
}
