@use "global" as *;

.hamburger {
    background-color: $pink;
    padding: rem(33) rem(21) rem(12);
}

.hamburger.open {
    background-color: $white;
    padding: rem(33) rem(18) rem(12);
    top: 0;
}

.hamburger span {
    display: block;
    width: rem(28);
    height: rem(3);
    background-color: $white;
    transition: 0.3s;
    position: relative;
}

.hamburger.open span {
    background-color: $pink;
}

.hamburger span:nth-child(2) {
    margin: rem(8) 0;
}

.hamburger span:nth-child(1)::before {
    content: "menu";
    position: absolute;
    top: rem(-25);
    left: rem(-7);
    color: $white;
    font-size: rem(14);
    line-height: calc(19 / 14);
    text-transform: uppercase;
}

.hamburger.open span:nth-child(1)::before {
    content: "close";
    color: $pink;
    transform: translateY(0) rotate(-45deg);
    top: rem(-25);
    left: rem(-25);
}

.hamburger.open span:nth-child(1) {
    transform: translate(7px, -1px) rotate(45deg);
    transform-origin: left;
    width: rem(35);
}

.hamburger.open span:nth-child(2) {
    opacity: 0;
}

.hamburger.open span:nth-child(3) {
    transform: translate(7px, 1px) rotate(-45deg);
    transform-origin: left;
    width: rem(35);
}
