@use "global" as *;

.menu__inner {
    margin-top: rem(80);
    @include mq(md) {
        margin-top: rem(160);
    }
}

.menu__title {
    font-family: $en-font-family;
    font-size: rem(18);
    line-height: calc(25 / 18);
    font-weight: $regular;
    color: $brown;
    width: 100%;
    padding-right: $padding-sp;
    padding-left: $padding-sp;
    margin-right: auto;
    margin-left: auto;
    @include mq(md) {
        font-size: rem(36);
        line-height: calc(48 / 36);
        max-width: $inner + strip-unit($padding-pc) * 2;
        padding-right: $padding-pc;
        padding-left: $padding-pc;
    }
}
