@use "global" as *;

.shop-list__img img {
    aspect-ratio: 300 / 165;
    width: 100%;
    object-fit: cover;
    @include mq(md) {
        aspect-ratio: 400 / 220;
    }
}

.shop-list__title {
    font-size: rem(24);
    line-height: calc(26 / 24);
    font-weight: $medium;
    color: $brown;
    display: flex;
    align-items: center;
    margin-top: rem(19);
    @include mq(md) {
        margin-top: rem(30);
    }
}

.shop-list__title span {
    font-size: rem(14);
    line-height: 1;
    margin-left: rem(11);
    @include mq(md) {
        margin-left: rem(15);
    }
}

.shop-list__text {
    font-size: rem(14);
    line-height: calc(24 / 14);
    letter-spacing: 0.0025em;
    font-weight: $semi-bold;
    margin-top: rem(18);
    @include mq(md) {
        font-size: rem(16);
        line-height: calc(27 / 16);
        letter-spacing: 0;
        margin-top: rem(15);
    }
}
