@use "global" as *;

.news__inner {
    width: 100%;
    padding-right: $padding-sp;
    padding-left: $padding-sp;
    margin-right: auto;
    margin-left: auto;
    @include mq(md) {
        max-width: $inner + strip-unit($padding-pc) * 2;
        padding-right: $padding-pc;
        padding-left: $padding-pc;
    }
}

.news__title-area {
    @include mq(md) {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}

.news__title-area::before {
    @include mq(md) {
        content: "";
        background: url(../images/line.svg);
        background-size: contain;
        background-repeat: no-repeat;
        display: inline-block;
        width: rem(464);
        height: rem(32.52);
    }
}

.news__title-area::after {
    @include mq(md) {
        content: "";
        background: url(../images/line.svg);
        background-size: contain;
        background-repeat: no-repeat;
        display: inline-block;
        width: rem(464);
        height: rem(32.52);
        transform: scaleX(-1);
    }
}

.news__lists {
    margin-top: rem(26);
    max-width: min(calc(300 / 375 * 100vw), 500px);
    margin-right: auto;
    margin-left: auto;
    @include mq(md) {
        margin-top: rem(83);
        max-width: initial;
    }
}
