@use "global" as *;

.accordion {
    position: relative;
}

.accordion__title {
    color: $white;
    cursor: pointer;
    padding: rem(14) rem(50) rem(14) rem(16);
    display: flex;
    align-items: center;
    font-size: rem(12);
    line-height: 1;
    font-weight: $semi-bold;
    @include mq(md) {
        padding: rem(27) rem(90) rem(29) rem(36);
        font-size: rem(24);
    }
}

.accordion__title span {
    font-family: $en-font-family;
    font-size: rem(24);
    line-height: calc(32 / 24);
    font-weight: $regular;
    display: inline-block;
    margin-right: rem(18);
    @include mq(md) {
        font-size: rem(36);
        line-height: calc(48 / 36);
        margin-right: rem(26);
    }
}

.accordion__text {
    display: none;
    padding: rem(14) 0;
    @include mq(md) {
        padding: rem(27) 0 rem(29);
    }
}

.accordion__text-inner {
    display: flex;
    align-items: baseline;
}

.accordion__text p {
    color: $pink;
    font-size: rem(12);
    line-height: calc(20 / 12);
    display: inline-block;
    margin-left: rem(23);
    padding-right: rem(20);
    @include mq(md) {
        font-size: rem(24);
        margin-left: rem(38);
    }
}

.accordion__text span {
    color: $pink;
    font-family: $en-font-family;
    font-size: rem(24);
    line-height: calc(32 / 24);
    font-weight: $regular;
    margin-left: rem(16);
    @include mq(md) {
        font-size: rem(36);
        line-height: calc(48 / 36);
        margin-left: rem(36);
    }
}

.accordion__list:nth-child(n + 2) {
    margin-top: rem(16);
    @include mq(md) {
        margin-top: rem(80);
    }
}

/* タイトルの背景色 */
.accordion__list .accordion__title {
    background-color: $pink;
}
/* コンテンツボックスの枠線 */
.accordion__list .accordion__text {
    border: 1px solid $pink;
}
/* 矢印 */
.accordion__title {
    position: relative;
}
/* 縦線 */
.accordion__title::before {
    background: $white; /* 線の色 */
    content: "";
    height: rem(2); /* 線の太さ */
    position: absolute;
    right: rem(25);
    top: 50%; /* 線の位置を縦中央に */
    transform: rotate(90deg); /* 線を縦に */
    transition: transform 0.3s ease-in-out;
    width: rem(20); /* 線の幅 */
    @include mq(md) {
        height: rem(5);
        width: rem(50);
    }
}
/* 横線 */
.accordion__title::after {
    background: $white; /* 線の色 */
    content: "";
    height: rem(2); /* 線の太さ */
    position: absolute;
    right: rem(25);
    top: 50%; /* 線の位置を縦中央に */
    transition: opacity 0.3s ease-in-out;
    width: rem(20); /* 線の幅 */
    @include mq(md) {
        height: rem(5);
        width: rem(50);
    }
}
/* 縦線(クリック後) */
.accordion__title.open::before {
    transform: rotate(180deg);
}
/* 横線(クリック後) */
.accordion__title.open::after {
    opacity: 0;
}
