@use "global" as *;

.header-nav {
    width: 100%;
    height: rem(460);
    background-color: $pink;
    // overflow-y: scroll;
    @include mq(md) {
        width: initial;
        height: inherit;
        // overflow-y: initial;
        background-color: initial;
    }
}

.header-nav__items {
    height: inherit;
    display: flex;
    flex-direction: column;
    margin-top: rem(3);
    margin-left: rem(16);
    min-height: rem(400);
    @include mq(md) {
        min-height: initial;
        flex-direction: row;
        align-items: center;
        margin-top: 0;
        margin-left: 0;
    }
}

.header-nav__item a {
    font-size: rem(24);
    line-height: calc(32 / 24);
    // text-transform: uppercase;
    font-family: $en-font-family;
    display: inline-block;
    padding-top: rem(16);
    color: $white;
    @include mq(md) {
        padding-top: 0;
        color: $brown;
        padding: rem(15) rem(8);
    }
}

.header-nav__item a:hover {
    opacity: 0.8;
    transition: 0.3s;
    @include mq(md) {
        opacity: 1;
        color: $pink;
    }
}

.header-nav__item--tel a {
    font-family: $base-font-family;
    text-transform: uppercase;
    font-weight: $semi-bold;
    background-color: $pink;
    color: $white;
    font-size: rem(24);
    line-height: 1;
    padding: rem(17) rem(30) rem(15);
    top: 0;
    right: 0;
    width: rem(100);
    margin-left: rem(15);
    opacity: 0.9;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: inherit;
    gap: rem(4);
}

.header-nav__item--tel a::after {
    content: "";
    background: url(../images/tel-icon.svg);
    display: inline-block;
    background-size: contain;
    width: rem(40);
    height: rem(40);
}
