@use "global" as *;

.header {
    background-color: $beige;
    opacity: 0.9;
    height: rem(70);
    width: 100%;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: 11;
    @include mq(md) {
        height: rem(100);
    }
}

.header__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: inherit;
}

.header__logo img {
    height: inherit;
    width: rem(190);
    padding-left: rem(10);
    padding-top: rem(8);
    @include mq(md) {
        width: rem(335);
        padding-left: rem(80);
        padding-top: 0;
    }
}

.header__logo a {
    height: inherit;
    display: flex;
    align-items: center;
}

.header__hamburger {
    position: relative;
    z-index: 20;
    @include mq(md) {
        display: none;
    }
}

.header__nav {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    @include mq(md) {
        display: block;
        position: static;
    }
}
