@use "global" as *;

.look__inner {
    margin-top: rem(26);
    @include mq(md) {
        margin-top: rem(83);
    }
}

.look__swiper-wrapper img {
    aspect-ratio: 300 / 184.83;
    width: 100%;
    object-fit: cover;
    max-width: min(calc(300 / 375 * 100vw), 500px);
    margin-right: auto;
    margin-left: auto;
    @include mq(md) {
        aspect-ratio: 870 / 536;
        max-width: min(calc(870 / 1440 * 100vw));
    }
}
