@use "global" as *;

.info__inner {
    margin-top: rem(28);
    @include mq(md) {
        margin-top: rem(83);
    }
}

.info__wrapper {
    @include mq(md) {
        display: flex;
        justify-content: space-between;
    }
}

.info__text-container {
    width: 100%;
    @include mq(md) {
        width: rem(630);
        height: rem(307);
    }
}

.info__text {
    font-size: rem(12);
    line-height: 1;
    font-weight: $medium;
    border-bottom: 0.5px solid $gray;
    padding-bottom: rem(8);
    display: flex;
    align-items: center;
    width: 100%;
    @include mq(md) {
        font-size: rem(24);
        padding-bottom: rem(12);
    }
}

.info__text:nth-child(n + 2) {
    margin-top: rem(6);
    @include mq(md) {
        margin-top: rem(11);
    }
}

.info__text:last-child {
    border-bottom: none;
    padding-bottom: 0;
}

.info__text th {
    min-width: rem(30);
    font-weight: $medium;
    @include mq(md){
        min-width: rem(60);
    }
}

.info__text td {
    margin-left: rem(15);
    @include mq(md){
        margin-left: rem(25);
    }
}

.info__text span{
    @include mq(md){
        font-size: rem(18);
    }
}

.info__img {
    margin-top: rem(25);
    @include mq(md) {
        width: rem(550);
        margin-top: 0;
    }
}

.info__img img {
    aspect-ratio: 355 / 201.38;
    width: 100%;
    object-fit: cover;
    @include mq(md) {
        aspect-ratio: 550 / 312;
    }
}

.info__map {
    width: 100%;
    height: rem(104);
    @include mq(md) {
        max-width: rem(1280);
        height: rem(375);
        margin-top: rem(80);
    }
}
