@use "global" as *;

.mv {
    height: rem(245);
    @include mq(md) {
        height: 100vh;
    }
}

.mv__swiper {
    position: relative;
    width: 100%;
    height: 100%;
}

.mv__swiper-wrapper img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    aspect-ratio: 375 / 245;
}

.mv__title {
    position: absolute;
    color: $white;
    top: 50%;
    left: 50%;
    transform: translateY(-50%) translateX(-50%);
    margin: auto;
    text-align: center;
    z-index: 10;
    width: 100%;
}

.mv__img {
    width: rem(50);
    margin: 0 auto;
    @include mq(md) {
        width: rem(250);
    }
}

.mv__logo-text {
    font-family: $en-font-family;
    font-size: rem(14);
    line-height: calc(19 / 14);
    margin-top: rem(5);
    @include mq(md) {
        font-size: rem(36);
        line-height: calc(48 / 36);
        margin-top: 0;
    }
}

.mv__text {
    font-size: rem(16);
    line-height: 1;
    font-weight: $semi-bold;
    margin-top: rem(11);
    @include mq(md) {
        font-size: rem(36);
        margin-top: rem(23);
    }
}
