@use "global" as *;

.form {
    padding-top: rem(42);
    @include mq(md) {
        padding-top: 0;
    }
}

.form__title {
    color: #502323;
    font-size: rem(14);
    line-height: 1;
    font-weight: $medium;
    @include mq(md) {
        font-size: rem(18);
    }
}

.form__item {
    max-width: rem(200);
    width: 100%;
    margin-right: auto;
    margin-left: auto;
    @include mq(md) {
        display: flex;
        // align-items: center;
        justify-content: space-between;
        max-width: rem(660);
    }
}

.form__item:nth-child(n + 2) {
    margin-top: rem(26);
    @include mq(md) {
        margin-top: rem(36);
    }
}

.form__item input[type="text"],
.form__item input[type="tel"],
.form__item input[type="email"] {
    background-color: $white;
    border: 1px solid #707070;
    width: 100%;
    height: rem(30);
    padding: rem(15);
    margin-top: rem(10);
    @include mq(md) {
        max-width: rem(500);
        margin-top: 0;
        height: rem(40);
    }
}

.form__item textarea {
    background-color: $white;
    border: 1px solid #707070;
    width: 100%;
    height: rem(80);
    padding: rem(15);
    margin-top: rem(10);
    @include mq(md) {
        max-width: rem(500);
        height: rem(170);
        margin-top: 0;
    }
}
