@use "global" as *;

.contact {
    @include mq(md) {
        background-color: #f4f4f4;
        padding-top: rem(72);
        max-width: rem(1280);
        padding-right: $padding-pc;
        padding-left: $padding-pc;
        margin-right: auto;
        margin-left: auto;
        width: 100%;
    }
}

.contact__inner {
    margin-top: rem(10);
    @include mq(md) {
        margin-top: rem(29);
    }
}

.contact__text {
    width: 100%;
    padding-right: $padding-sp;
    padding-left: $padding-sp;
    margin-right: auto;
    margin-left: auto;
    max-width: 500px;
    text-align: center;
    @include mq(md) {
        max-width: initial;
    }
}

.contact__text p {
    color: #502323;
    font-size: rem(14);
    font-weight: $medium;
    line-height: calc(24 / 14);
    text-align: left;
    display: inline-block;
    @include mq(md) {
        text-align: center;
        font-size: rem(18);
        line-height: calc(31 / 18);
    }
}

.contact__form-wrapper {
    background-color: #f4f4f4;
    margin-top: rem(16);
    padding-bottom: rem(56);
    @include mq(md) {
        background-color: transparent;
        margin-top: rem(83);
        padding-bottom: rem(96);
    }
}

.contact__btn {
    margin-top: rem(40);
    text-align: center;
    @include mq(md) {
        margin-top: rem(80);
    }
}

.contact__btn a {
    color: $white;
    background-color: $pink;
    font-size: rem(16);
    line-height: 1;
    font-weight: $medium;
    border-radius: rem(8);
    padding: rem(18) rem(68) rem(16);
    @include mq(md) {
        font-size: rem(18);
        padding: rem(16) rem(64);
    }
}
